export default {
	globalData: {
		text: 'text'
	},
	computed: {
	},
	data() {
		return {
			fleer: 0, //左边的菜单的高度
			ssdfidx: 0,
			isdxdf: 1, //1显示横 2显示竖
			ddf_ddr: [{ //左边的菜单
				cls: "act"
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}, {
				cls: ""
			}],
			tuoosd: [{
					name: "推荐",
					id: ""
				},
				{
					name: "生鲜水果",
					id: ""
				},
				{
					name: "海鲜水产",
					id: ""
				},
				{
					name: "异国风味",
					id: ""
				},
				{
					name: "优选饮食",
					id: ""
				}
			]
		}
	},
	components: {
		lists,
		lister
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	methods: {
		qiehuan(sd) { //左边的菜单点击事件
			this.ddf_ddr.map(a => {
				a.cls = ''
			})
			sd.cls = "act"
		},
		ssdrfrt(idx) {
			this.ssdfidx = idx
		}
	},
	onShow() {},
	onLoad() {
		uni.$on('popup-page', (data) => {
			this.isdxdf = data.issdrr
		})
		//  高度自适应
		uni.getSystemInfo({
			success: (res) => {
				let calc = res.windowHeight - uni.upx2px(80);
				this.fleer = calc + 40
			}
		});
	},
	mounted() {}
}
